import Vue from 'vue'
import VueRouter from 'vue-router'
import Content from '../components/Content.vue';
import ContentNotop from '../components/ContentNotop.vue';
import Home from '../views/Home.vue'
import Open from '../views/open.vue';
import Buy from '../views/Buy.vue';
import Else from '../views/Else.vue';
import Daily from '../views/Daily.vue';
import Art from '../views/Art.vue';
import Commodity from '../views/Commodity.vue';
import ArtDetail from '../views/ArtDetail.vue';
import Search from '../views/Search.vue';
import MasterDetail from '../views/MasterDetail.vue';
import Recycle from '../views/Recycle.vue';

Vue.use(VueRouter)

const routes = [{
        path: '/',
        meta: { index: 0 },
        name: 'open',
        component: Open
    }, {
        path: '/content',
        name: 'Content',
        component: Content,
        children: [{
            path: '/content',
            component: Home,
        }, {
            path: '/content/homepage',
            component: Home,
        }, {
            path: '/content/buy',
            component: Buy,
        }, ]
    }, {
        path: '/contentnt',
        name: 'Contentnt',
        component: ContentNotop,
        children: [{
            path: '/contentnt/else',
            component: Else,
        }, {
            path: '/contentnt/masterdetail',
            component: MasterDetail,
        }, ]
    }, {
        path: '/daily',
        name: 'Daily',
        component: Daily
    }, {
        path: '/art',
        name: 'Art',
        component: Art
    }, {
        path: '/commodity',
        name: 'Commodity',
        component: Commodity
    }, {
        path: '/artdetail',
        name: 'ArtDetail',
        component: ArtDetail
    }, {
        path: '/search',
        name: 'Search',
        component: Search
    }, {
        path: '/recycle',
        name: 'recycle',
        component: Recycle
    },

]

const router = new VueRouter({
    routes
})

export default router